/* Buttons
 */
.btn,
button {
  display: inline-block;
  padding: 1.5rem 3rem;
  color: #222;
  text-align: center;
  font-size: 1.2rem;
  font-weight: 700;
  letter-spacing: .1rem;
  text-transform: uppercase;
  text-decoration: none;
  white-space: nowrap;
  border-radius: 2px;
  cursor: pointer;
  box-sizing: border-box;
  transition: all 0.3s;
}
.btn-primary,
button-primary {
  background-color: #f7c305;
}
.btn-primary:hover,
button-primary:hover {
  background: #e0b415;
}
.btn-primary:focus,
button-primary:focus {
  background: #c7a31f;
}
.btn-grey,
button-grey {
  background: #e2e2e2;
}
.btn-grey:hover,
button-grey:hover {
  background: #f7c305;
}
.btn-grey:focus,
button-grey:focus {
  background: #c7a31f;
}
.btn-green,
button-green {
  color: #fff;
  background: #6dac12;
}
.btn-green:hover,
button-green:hover {
  background: #528509;
}
.btn-green:focus,
button-green:focus {
  background: #87c133;
}
