/* --- EASYDROPDOWN DEFAULT THEME --- */
/* PREFIXED CSS */
.dropdown,
.dropdown div,
.dropdown li,
.dropdown div::after {
  -webkit-transition: all 150ms ease-in-out;
  -moz-transition: all 150ms ease-in-out;
  -ms-transition: all 150ms ease-in-out;
  transition: all 150ms ease-in-out;
}
.dropdown .selected::after,
.dropdown.scrollable div::after {
  -webkit-pointer-events: none;
  -moz-pointer-events: none;
  -ms-pointer-events: none;
  pointer-events: none;
}
/* WRAPPER */
.dropdown {
  color: inherit;
  font: inherit;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #e9e9e9;
  -webkit-border-radius: 0;
  border-radius: 0;
  outline: none;
  height: 3rem;
  width: 100%;
  font-size: 1.4rem;
  margin: 0 0 3rem 0;
  padding: 0;
  box-shadow: none;
  box-sizing: content-box;
  transition: all 0.3s;
  cursor: pointer;
  user-select: none;
}
.dropdown.open {
  z-index: 2;
}
.dropdown.err {
  border-bottom: 1px solid #ff5151;
  box-shadow: 0 1px 0 0 #ff5151;
}
/* CARAT */
.dropdown .carat {
  position: absolute;
  right: 12px;
  margin-top: -10px;
  border: 4px solid transparent;
  border-top: 8px solid #000;
}
.dropdown.open .carat {
  margin-top: -18px;
  border-top: 6px solid transparent;
  border-bottom: 8px solid #000;
}
.dropdown.disabled .carat {
  border-top-color: #999;
}
/* OLD SELECT (HIDDEN) */
.dropdown .old {
  position: absolute;
  left: 0;
  top: 0;
  height: 0;
  width: 0;
  overflow: hidden;
}
.dropdown select {
  position: absolute;
  left: 0px;
  top: 0px;
}
.dropdown.touch .old {
  width: 100%;
  height: 100%;
}
.dropdown.touch select {
  width: 100%;
  height: 100%;
  opacity: 0;
}
/* SELECTED FEEDBACK ITEM */
.dropdown .selected,
.dropdown li {
  display: block;
  text-align: left;
  margin-bottom: 0;
}
.dropdown.disabled .selected {
  color: #999;
}
/* DROP DOWN WRAPPER */
.dropdown div {
  position: absolute;
  height: 0;
  left: -1px;
  right: -1px;
  top: 0;
  margin-top: -1px;
  background: #fff;
  box-shadow: 0 2px 5px 0 rgba(0, 0, 0, 0.16), 0 2px 10px 0 rgba(0, 0, 0, 0.12);
  overflow: hidden;
  opacity: 0;
}
/* Height is adjusted by JS on open */
.dropdown.open div {
  opacity: 1;
  z-index: 2;
}
/* FADE OVERLAY FOR SCROLLING LISTS */
.dropdown.scrollable div::after {
  content: '';
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  height: 50px;
  box-shadow: inset 0 -50px 30px -35px #fff;
}
.dropdown.scrollable.bottom div::after {
  opacity: 0;
}
/* DROP DOWN LIST */
.dropdown ul {
  position: absolute;
  left: 0;
  top: 0;
  height: 100%;
  width: 100%;
  list-style: none;
  overflow: hidden;
}
.dropdown.scrollable.open ul {
  overflow-y: auto;
}
/* DROP DOWN LIST ITEMS */
.dropdown li {
  list-style: none;
  padding: 8px 12px;
}
/* .focus class is also added on hover */
.dropdown li.focus {
  background: #f7c305;
  position: relative;
  z-index: 3;
}
.dropdown li.active {
  font-weight: 700;
}
