textarea {
  resize: none;
}
a {
  text-decoration: none;
  transition: all 0.3s;
}
.inner {
  overflow: hidden;
  padding-left: 230px;
}
[type="submit"] {
  cursor: pointer;
}
.video {
  position: relative;
  padding-bottom: 56.25%;
  height: 0;
  overflow: hidden;
}
.video iframe,
.video object,
.video embed {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
/*=== begin header style ===*/
.header {
  position: fixed;
  left: 230px;
  right: 0;
  top: 0;
  background-color: rgba(32, 32, 32, 0.5);
  z-index: 7999;
  transition: 0.2s background ease-out;
}
.header.fix {
  background-color: #202020;
}
.header__wrap {
  display: flex;
  justify-content: space-between;
  flex-flow: row wrap;
  align-items: center;
  height: 8rem;
}
.overlay {
  display: none;
  content: '';
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: rgba(0, 0, 0, 0.5);
  z-index: 7998;
}
.h-logo {
  display: none;
}
