._burger {
  display: none;
}
.toggle {
  display: none;
  position: relative;
}
.toggle__title {
  display: block;
  padding: 12px 10px 13px 45px;
  font-size: 12px;
  color: #505050;
  text-transform: uppercase;
  color: #fff;
  cursor: pointer;
}
.toggle__btn {
  color: #fff;
  padding: 7px 8px;
  position: absolute;
  top: 6px;
  left: 0;
  cursor: pointer;
  transition: left .23s;
  z-index: 3;
  width: 37px;
  -webkit-transform: translateZ(0);
  -webkit-backface-visibility: hidden;
  box-sizing: border-box;
  border: 1px solid #343434;
}
.toggle__btn span {
  display: block;
  height: 2px;
  background-color: #fff;
  margin: 4px 0 0;
  transition: all .1s;
  position: relative;
}
.toggle__btn span.toggle__first {
  margin-top: 0;
}
.toggle.active .toggle__btn span.toggle__first {
  -webkit-transform: rotate(45deg);
  top: 6px;
}
.toggle.active .toggle__btn span.toggle__second {
  opacity: 0;
}
.toggle.active .toggle__btn span.toggle__third {
  -webkit-transform: rotate(-45deg);
  top: -6px;
}
.h-menu {
  position: relative;
  margin-left: 250px;
}
.h-menu__main:before,
.h-menu__main:after {
  content: " ";
  display: table;
}
.h-menu__main > li {
  position: relative;
  display: table-cell;
  width: 1%;
  text-align: center;
  white-space: nowrap;
}
.h-menu__main > li._contacts {
  display: none;
}
.h-menu__main > li.opened > a {
  color: #ffffff;
  background-color: rgba(255, 255, 255, 0.03);
}
.h-menu__main > li.opened > .h-menu__sub {
  display: block;
}
.h-menu__main > li > a {
  display: block;
  padding: 40px 0;
  font-size: 14px;
  color: #daebfa;
  font-weight: bold;
  text-transform: uppercase;
}
.h-menu__main > li > a.drop:after {
  content: '';
  display: inline-block;
  margin-left: 7px;
  width: 6px;
  height: 6px;
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  -webkit-transform: rotate(-45deg);
  transform: rotate(-45deg);
  margin-bottom: 4px;
}
.h-menu__main > li > a.drop + .h-menu__arrow {
  display: none;
}
.h-menu__sub {
  display: none;
  z-index: 10;
  position: absolute;
  background-color: #ffffff;
  box-shadow: 0px 0px 13px 0px rgba(0, 0, 0, 0.25);
}
.h-menu__sub > li {
  position: relative;
  width: 190px;
}
.h-menu__sub > li > a {
  display: block;
  text-align: left;
  padding-left: 20px;
  line-height: 50px;
  font-size: 12px;
  color: #092744;
  font-weight: bold;
  text-transform: uppercase;
}
.h-menu__sub > li.active > a {
  background-color: #f5f6f7;
  color: #4077aa;
}
.h-menu__sub > li.active > .h-menu__sub {
  display: block;
}
.h-menu__sub > li.active > a.nested {
  color: #092744;
}
.h-menu__sub_nested {
  display: none;
  top: 0;
  left: 190px;
}
.h-menu__sub_nested > li {
  width: 190px;
}
.h-menu__sub_nested > li > a {
  position: relative;
  display: block;
  text-align: left;
}
.h-menu__arrow {
  position: absolute;
  top: 0;
  right: 0;
  height: 50px;
  line-height: 50px;
  width: 40px;
}
.h-menu__arrow:after {
  content: '';
  display: inline-block;
  margin-left: 7px;
  width: 6px;
  height: 6px;
  border-bottom: 2px solid #acb1b7;
  border-left: 2px solid #acb1b7;
  -webkit-transform: rotate(225deg);
  transform: rotate(225deg);
  margin-bottom: 4px;
}
.popup {
  position: relative;
  z-index: 9999;
  display: none;
  padding: 5rem;
  background-color: #ffffff;
}
.popup._advice {
  max-width: 580px;
}
.popup .section__title {
  margin-bottom: 2rem;
}
.popup .section__pre {
  padding-top: 5px;
  padding-bottom: 3rem;
}
.popup .form__btn {
  text-align: center;
}
/*=== begin footer style ===*/
.footer {
  position: relative;
  z-index: 1;
}
.footer__title {
  font-size: 16px;
  color: #ffffff;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 40px;
}
.footer__copyright {
  border-top: 1px solid #f7f7f7;
  background-color: #fff;
  text-align: center;
  font-size: 1.2rem;
  padding: 3rem 0;
}
.f-back-top {
  display: none;
  border-radius: 50%;
  background-color: #f7c305;
  position: fixed;
  right: 15px;
  bottom: 15px;
  z-index: 100;
  width: 42px;
  height: 42px;
  cursor: pointer;
  user-select: none;
}
.f-back-top:after {
  content: '';
  display: block;
  width: 8px;
  height: 8px;
  border-bottom: 2px solid #fff;
  border-left: 2px solid #fff;
  transform: rotate(135deg);
  position: absolute;
  top: 19px;
  left: 18px;
}
.f-top {
  font-size: 0;
  margin-bottom: 40px;
}
.f-form {
  float: left;
  width: 500px;
  height: 600px;
  padding-top: 5rem;
}
.f-map {
  padding-left: 500px;
}
.form {
  margin: 0 auto;
  max-width: 330px;
}
.form__block {
  position: relative;
  padding-bottom: 1px;
}
.form__services {
  position: relative;
  padding-bottom: 1px;
}
.input {
  color: inherit;
  font: inherit;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #e9e9e9;
  border-radius: 0;
  outline: none;
  height: 3rem;
  width: 100%;
  font-size: 1.4rem;
  margin: 0 0 3rem 0;
  padding: 0;
  box-shadow: none;
  box-sizing: content-box;
  transition: all 0.3s;
}
.input.active {
  border-bottom: 1px solid #f7c305;
  box-shadow: 0 1px 0 0 #f7c305;
}
.input.active + .label {
  font-size: 1.2rem;
  transform: translateY(-100%);
  color: #f7c305;
}
.input.err {
  border-bottom: 1px solid #ff5151;
  box-shadow: 0 1px 0 0 #ff5151;
}
.err-text {
  position: absolute;
  bottom: 1rem;
  left: 0;
  font-size: 12px;
  color: #ff5151;
}
.label {
  color: #6d6d6d;
  position: absolute;
  top: 0.5rem;
  left: 0;
  font-size: 1.4rem;
  cursor: text;
  transition: 0.2s ease-out;
  text-align: left;
}
.label:after {
  display: block;
  content: "";
  position: absolute;
  top: 60px;
  opacity: 0;
  transition: 0.2s opacity ease-out, 0.2s color ease-out;
}
.select {
  color: inherit;
  font: inherit;
  background-color: transparent;
  border: none;
  border-bottom: 1px solid #e9e9e9;
  -webkit-border-radius: 0;
  border-radius: 0;
  outline: none;
  height: 3rem;
  width: 100%;
  font-size: 1.4rem;
  margin: 0 0 3rem 0;
  padding: 0;
  box-shadow: none;
  box-sizing: content-box;
  transition: all 0.3s;
}
.map {
  height: 600px;
}
.map__item {
  margin: 1rem 0 0.5rem;
  padding: 0;
  overflow: hidden;
}
.map__img {
  float: left;
  width: 178px;
  height: 178px;
}
.map__img img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}
.map__info {
  margin-left: 200px;
}
.map__title {
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 1rem;
}
.map__text {
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.map__metro {
  font-size: 1.4rem;
  margin-bottom: 1rem;
  position: relative;
  padding-left: 25px;
}
.map__metro i {
  position: absolute;
  width: 23px;
  height: 23px;
  top: 0;
  left: 0;
  background: url('../images/icon-metro.jpg') no-repeat;
}
.map__number {
  font-size: 1.4rem;
  font-family: "Museo Sans Cyrl 900";
  margin-bottom: 1rem;
}
.map__callback {
  font-size: 1.4rem;
  font-weight: bold;
  margin-bottom: 1rem;
  color: #333;
  border-bottom: 1px dotted #6d6d6d;
}
@media (max-width: 767px) {
  .form__block_wide .input {
    margin-top: 15px;
  }
}
.sidebar {
  position: fixed;
  width: 230px;
  top: 0;
  bottom: 0;
  left: 0;
  border-right: 1px solid #f7f7f7;
  z-index: 8000;
  display: flex;
  align-content: space-between;
  flex-flow: row wrap;
  background-color: #fff;
  transition: left .3s;
}
.s-block {
  width: 100%;
}
.s-logo {
  margin: 6rem;
}
