.menu {
  position: relative;
}
.menu__main {
  margin: 0;
  padding: 0;
  list-style: none;
}
.menu__main > li {
  position: relative;
  text-align: left;
  white-space: nowrap;
  margin-bottom: 0;
}
.menu__main > li.opened {
  background-color: #202020;
}
.menu__main > li.opened > a {
  color: #fff;
}
.menu__main > li.opened > .menu__sub {
  display: block;
}
.menu__main > li > a {
  display: block;
  margin: 0 2rem;
  padding-left: 1rem;
  height: 6rem;
  line-height: 6rem;
  font-size: 1.4rem;
  color: #202020;
  font-weight: bold;
  text-transform: uppercase;
  border-bottom: 1px dotted #bbbbbb;
}
.menu__main > li > a.drop {
  position: relative;
}
.menu__main > li > a.drop:after {
  content: '';
  display: block;
  position: absolute;
  top: 2.6rem;
  right: 0;
  width: 6px;
  height: 6px;
  border-bottom: 2px solid #7e7e7e;
  border-left: 2px solid #7e7e7e;
  transform: rotate(-135deg);
  margin-bottom: 4px;
}
.menu__main > li > a.drop + .menu__arrow {
  display: none;
}
.menu__main > li:last-child > a {
  border-bottom: 0;
}
.menu__sub {
  display: none;
  width: 300px;
  margin: 0;
  padding: 0;
  list-style: none;
  z-index: 10;
  position: fixed;
  top: 0;
  bottom: 0;
  left: 229px;
  padding-top: 240px;
  background-color: #202020;
}
.menu__sub > li {
  position: relative;
  margin-bottom: 0;
}
.menu__sub > li:last-child > a {
  border-bottom: 0;
}
.menu__sub > li > a {
  display: block;
  margin: 0 3rem;
  padding-left: 1rem;
  height: 3.5rem;
  line-height: 3.5rem;
  font-size: 1.3rem;
  color: #ffffff;
  border-bottom: 1px dotted #bbbbbb;
}
.menu__sub > li > a.nested {
  position: relative;
}
.menu__sub > li > a.nested:after {
  content: '';
  display: block;
  position: absolute;
  top: 1.6rem;
  right: 0;
  width: 6px;
  height: 6px;
  border-bottom: 2px solid #7e7e7e;
  border-left: 2px solid #7e7e7e;
  transform: rotate(-135deg);
  margin-bottom: 4px;
}
.menu__sub > li.active {
  background-color: #191919;
}
.menu__sub > li.active > .menu__sub {
  display: block;
}
.menu__sub_nested {
  display: none;
  width: auto;
  left: 529px;
  top: 0;
  background-color: #191919;
  transform: translateY(0);
}
.menu__sub_nested > li.active {
  background: #f7c305;
}
.menu__sub_nested > li.active a {
  color: #000;
}
.menu__sub_nested > li > a {
  position: relative;
  display: block;
  text-align: left;
}
.menu__arrow {
  display: none;
}
.s-discover {
  margin-left: 2rem;
  margin-bottom: 3rem;
}
.s-discover .btn {
  padding: 1.5rem 2rem;
  box-shadow: 0px 1px 32px 0px rgba(247, 197, 5, 0.7), inset 0px 3px 32px 0px rgba(255, 255, 255, 0.18);
