@media (max-width: 767px) {
  .menu__main > li {
    position: relative;
    text-align: left;
    white-space: nowrap;
    margin-bottom: 0;
  }
  .menu__main > li.opened .drop + .menu__arrow:after {
    transform: rotate(315deg);
  }
  .menu__main > li.opened > .menu__sub {
    display: none;
  }
  .menu__main > li > a {
    margin: 0;
  }
  .menu__main > li > a.drop:after {
    content: none;
  }
  .menu__main > li > a.drop + .menu__arrow {
    display: block;
  }
  .menu__sub {
    width: auto;
    position: relative;
    top: 0;
    left: 0;
    transform: translateY(0);
    padding: 0;
  }
  .menu__sub .submenu__arrow {
    position: absolute;
    top: 0;
    right: 0;
    height: 30px;
    line-height: 30px;
    width: 6rem;
    text-align: center;
    border-left: 1px dotted #bbbbbb;
  }
  .menu__sub .submenu__arrow:after {
    content: '';
    display: inline-block;
    width: 6px;
    height: 6px;
    border-bottom: 2px solid #acb1b7;
    border-left: 2px solid #acb1b7;
    -webkit-transform: rotate(225deg);
    transform: rotate(225deg);
    margin-bottom: 2px;
  }
  .menu__sub > li {
    position: relative;
    margin-bottom: 0;
  }
  .menu__sub > li:first-child > a {
    border-top: 0;
  }
  .menu__sub > li > a {
    margin: 0;
  }
  .menu__sub > li > a.nested:after {
    content: none;
  }
  .menu__sub > li.active {
    background-color: #191919;
  }
  .menu__sub > li.active > .menu__sub {
    display: none;
  }
  .menu__sub > li.active .nested + .submenu__arrow:after {
    transform: rotate(315deg);
  }
  .menu__sub_nested {
    width: auto;
    left: 0;
  }
  .menu__sub_nested > li.active {
    background: #f7c305;
  }
  .menu__sub_nested > li.active a {
    color: #000;
  }
  .menu__sub_nested > li > a {
    padding-left: 2rem;
  }
  .menu__arrow {
    position: absolute;
    top: 0;
    right: 0;
    height: 6rem;
    line-height: 6rem;
    width: 6rem;
    text-align: center;
    border-left: 1px dotted #bbbbbb;
  }
  .menu__arrow:after {
    content: '';
    display: inline-block;
    width: 6px;
    height: 6px;
    border-bottom: 2px solid #acb1b7;
    border-left: 2px solid #acb1b7;
    -webkit-transform: rotate(225deg);
    transform: rotate(225deg);
    margin-bottom: 2px;
  }
}
