.pagin {
  display: inline-block;
  list-style: none;
  margin: 0;
  padding: 0;
  border-radius: 4px;
}
.pagin__item {
  display: inline;
}
.pagin__item:first-child a {
  margin-left: 0;
}
.pagin__item:hover a,
.pagin__item.active a {
  background-color: #f7c305;
}
.pagin__link {
  width: 46px;
  height: 46px;
  line-height: 46px;
  text-align: center;
  font-size: 1.6rem;
  font-weight: bold;
  position: relative;
  float: left;
  text-decoration: none;
  color: #363636;
  background-color: #fff;
  border: 1px solid #ddd;
  margin-left: -1px;
  transition: all 0.3s;
}
.pagin__link.word {
  width: 120px;
  background-color: #fafafa;
  font-size: 1.2rem;
  text-transform: uppercase;
}
@media (max-width: 767px) {
  .pagin__link.word {
    display: none;
  }
}
.number {
  flex-shrink: 0;
}
.number span {
  display: inline-block;
  font-size: 60px;
  font-family: 'BebasNeueBold';
  width: 110px;
  height: 110px;
  border-radius: 50%;
  background-color: #f7c305;
  text-align: center;
  line-height: 90px;
  border: 15px solid #fdf3cd;
}
.number_white span {
  background-color: #fff;
}
