.working .row {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.working__item {
  display: flex;
  flex-wrap: wrap;
  align-content: space-between;
  margin-bottom: 3rem;
}
.working__img {
  margin-bottom: 3rem;
}
.working__img span {
  display: block;
  width: 100px;
  height: 100px;
  background-repeat: no-repeat;
  background-position: 0% 50%;
}
.working__info {
  display: flex;
  align-content: space-between;
  flex-flow: row wrap;
}
.working__title {
  width: 100%;
  font-size: 1.8rem;
  font-weight: bold;
  margin-bottom: 3rem;
  -ms-flex: 0 1 auto;
}
.working__text {
  font-size: 1.4rem;
  font-weight: 300;
  margin-bottom: 2rem;
  -ms-flex: 0 1 auto;
}
.working__btn {
