.banner {
  height: 600px;
  padding: 0;
  background: transparent;
}
.banner__img {
  height: 600px;
  position: fixed;
  left: 230px;
  right: 0;
  top: 0;
  background-size: cover;
  background-position: 50% 0;
  z-index: -100;
}
.banner__img:after {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.5);
}
.banner__info {
  padding: 8rem 3rem 0;
  position: absolute;
  top: 50%;
  left: 0;
  right: 0;
  transform: translateY(-50%);
  z-index: 2;
  text-align: center;
}
.banner__title {
  font-size: 4.6rem;
  font-weight: bold;
  text-transform: uppercase;
  word-break: break-word;
}
.banner__text {
  font-size: 1.8rem;
  max-width: 960px;
  margin: 0 auto;
}
.banner__type {
  margin-top: 3rem;
  display: inline-block;
  border-radius: 2px;
  background-color: #fdf3cd;
}
.banner__type span {
  display: block;
  padding: 0.5rem 2rem;
  font-size: 12px;
  color: #232323;
  font-weight: bold;
  text-transform: uppercase;
}
.banner_default .banner__title {
  color: #fff;
}
.banner_default .banner__text {
