  right: 3rem;
}
.slider__btn:hover {
  background-color: rgba(35, 35, 35, 0.1);
}
.slider__item {
  position: relative;
}
.slider__item img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}
.slider__img {
  position: fixed;
  left: 230px;
  right: 0;
  top: 0;
  background-image: url(../images/slide01.jpg);
  background-size: cover;
  background-position: 50% 0;
  z-index: -100;
}
.slider__img img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}
.slider__info {
