}
.slider__text {
  font-size: 2.3rem;
  color: #ffffff;
  font-weight: bold;
  margin-bottom: 8rem;
}
.slider__link .btn {
  font-size: 1.6rem;
}
.about .tabs__pagin {
  text-align: center;
}
.about .tabs__btn {
  display: inline-block;
  padding: 1rem 3rem;
  border-radius: 2px;
  background-color: #e2e2e2;
  font-size: 1.6rem;
  font-weight: bold;
  margin: 0 1.5rem;
  margin-bottom: 5rem;
  transition: all 0.3s;
  cursor: pointer;
}
.about .tabs__btn span {
  border-bottom: 1px dotted;
}
.about .tabs__btn:hover,
.about .tabs__btn.active {
  background: #f7c305;
}
.about .tabs__btn:focus {
  background: #f7c305;
}
.about .tabs__item {
  font-size: 0;
  display: flex;
}
.about .tabs__img {
  background: #f7c305;
