  width: 25%;
  padding: 2rem;
  display: flex;
  justify-content: center;
  align-items: center;
}
.about .tabs__img img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}
.about .tabs__info {
  width: 75%;
  display: inline-block;
  vertical-align: middle;
  padding: 5rem;
  background: #fff;
}
.about .tabs__title {
  font-size: 2.4rem;
  font-weight: bold;
  text-transform: uppercase;
  margin-bottom: 3rem;
}
.about .tabs__text {
  font-size: 1.4rem;
}
.persona {
  padding-bottom: 5rem;
}
.persona__wrap {
  font-size: 0;
}
.persona__item {
