  display: inline-block;
  vertical-align: top;
  width: 33.33333333%;
  z-index: 1;
}
.persona__item img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}
.persona__info {
  background-color: #fff;
  padding: 5px 0;
  text-align: center;
  z-index: 10;
  transition: all 0.3s;
}
.persona__name {
  font-weight: bold;
  font-size: 1.8rem;
}
.persona__prof {
  font-size: 1.4rem;
}
.persona__quality {
  font-size: 1.4rem;
}
.portfolio {
  position: relative;
}
.portfolio__wrap {
  font-size: 0;
}
.portfolio__item {
  position: relative;
  z-index: 1;
  margin: 0;
  display: inline-block;
  vertical-align: top;
  float: left;
  width: 31.5%;
  overflow: hidden;
}
.portfolio__item._main {
  width: 37%;
}
.portfolio__item:before {
  content: '';
  position: absolute;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  opacity: 0;
  background: rgba(0, 0, 0, 0.9);
  transition: all 0.3s;
  transform: translateY(50%);
  z-index: 5;
}
.portfolio__item img {
  display: inline-block;
  max-width: 100%;
  height: auto;
  position: relative;
  z-index: 1;
  width: 100%;
}
.portfolio__item:hover:before {
  transform: translateY(0);
  opacity: 1;
}
.portfolio__item:hover .portfolio__info {
  opacity: 1;
  transform: translateY(50%);
  bottom: 50%;
}
.portfolio__info {
  position: absolute;
  left: 0;
  right: 0;
  bottom: 0;
  opacity: 0;
  text-align: center;
  z-index: 10;
  transition: all 0.3s;
}
.portfolio__name {
  font-size: 2.4rem;
  color: #fff;
  font-weight: bold;
  margin-bottom: 1rem;
}
.portfolio__text {
