  top: 7rem;
  right: 9rem;
}
.client {
  padding: 0;
}
.client__wrap {
  text-align: center;
  background-color: #fff;
  padding: 3rem;
}
.client__item {
  display: inline-block;
  vertical-align: top;
  width: 180px;
  text-align: center;
  margin: 1rem;
}
.client__img {
  height: 80px;
  line-height: 80px;
}
.client__img img {
  display: inline-block;
  max-width: 100%;
  height: auto;
}
.client__title {
  font-size: 1.4rem;
  font-weight: bold;
}
.client__text {
  font-size: 1.2rem;
}
.client__btn {
  text-align: center;
}
.client__btn .btn {
  margin: 3rem 0px 5rem;
}
.review {
  position: relative;
  padding-bottom: 0;
}
.review__btn {
  position: absolute;
  top: 7rem;
  right: 9rem;
}
.review__item {
  padding: 2rem;
  border: 1px solid transparent;
  margin-bottom: 5rem;
  transition: border-color 0.3s;
