}
.review__title {
  display: block;
  margin-bottom: 0.5rem;
}
.review__title span {
  font-size: 1.8rem;
  font-weight: bold;
  border-bottom: 1px solid #e0e0e0;
  color: #222;
  transition: border-color 0.3s;
}
.review__title:hover span {
  border-color: #f7c305;
}
.review__date {
  color: #6d6d6d;
  font-size: 1.2rem;
  margin-bottom: 3rem;
}
.review__text {
  font-size: 1.4rem;
  margin-bottom: 1rem;
}
.review__more a {
  font-size: 1.2rem;
  font-weight: bold;
  text-transform: uppercase;
  color: #222;
}
.review__more a:hover {
  color: #f7c305;
